import React from 'react';
import ReactDOM from 'react-dom';
import {applyMiddleware, createStore, compose} from 'redux';
import {Route, BrowserRouter, HashRouter, Switch, Redirect} from "react-router-dom";
import {Provider} from 'react-redux';
import createSagaMiddleware from "redux-saga";
import reducer from "./reducers/index";
import rootSaga from "./sagas";

import {CheckWebBrowser} from "./lib/script";

import IndexLayout from "./pages/index";
import LoginLayout from "./pages/login/Login";
import ErrorPage from "./pages/error/ErrorPage";
import Ie10 from "./pages/ie/Ie10";
import {getCookie} from "./action/cookie/Cookie";
import BottleLayout from "./pages/product/bottle";
import PeepeeLayout from "./pages/product/peepee";
import TempLayout from "./pages/product/temp";

const root = document.getElementById('app');

const sagaMiddleware = createSagaMiddleware();

//  
const reduxDevTools = window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__();

//  (production devtool )
let store = createStore(
    reducer,
    compose(applyMiddleware(sagaMiddleware),
        window.navigator.userAgent.includes('Chrome') ? reduxDevTools : compose)
);

/*   log */
store.subscribe(function (e) {
    console.log(store.getState());
});

/* */
sagaMiddleware.run(rootSaga);

/*IE */
const ieVersion = CheckWebBrowser();

/*       */
const LauchableVersion = {
    ie:10,
    firefox:'',
    opera:'',
    mozilla:'',
    chrome:'',
    safari:'',
};

//  
window.addEventListener('resize',function(){
    store.dispatch({
        type:'SET_WINDOW_WIDTH_REQUEST',
    })
});
//cookie   
let appState = store.getState();

if('ko'===appState.languageReducer.language){
    document.getElementsByTagName('body')[0].setAttribute('class','lang-ko');
    document.getElementsByTagName('html')[0].setAttribute('lang','ko');
} else if('ja'=== appState.languageReducer.language){
    document.getElementsByTagName('body')[0].setAttribute('class','lang-ja');
    document.getElementsByTagName('html')[0].setAttribute('lang','ja');
} else if('zh'=== appState.languageReducer.language){
    document.getElementsByTagName('body')[0].setAttribute('class','lang-zh');
    document.getElementsByTagName('html')[0].setAttribute('lang','zh');
} else if('en' === appState.languageReducer.language){
    document.getElementsByTagName('body')[0].setAttribute('class','lang-en');
    document.getElementsByTagName('html')[0].setAttribute('lang','en');
} else {
    document.getElementsByTagName('body')[0].setAttribute('class','lang-ko');
    document.getElementsByTagName('html')[0].setAttribute('lang','ko');
}

/* 10    */
if(ieVersion < LauchableVersion.ie){
    ReactDOM.render(
        <Ie10/>,root
    )
} else{
    ReactDOM.render(
        <Provider store={store}>
            <HashRouter>
                <Switch>
                    <Route exact path={"/"} component={IndexLayout}/>
                    <Route path={"/login"} component={LoginLayout}/>
                    <Route path="/404error" component={ErrorPage} notFound/>
                    <Route exact path={'/device/smartbottle'} component={BottleLayout}/>
                    <Route exact path={'/device/smartpeepee'} component={PeepeeLayout}/>
                    <Route exact path={'/device/smarttemp'} component={TempLayout}/>
                    <Redirect from="*" to="/404error"/>
                </Switch>
            </HashRouter>
        </Provider>, root
    );

}


